@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap);
.TimeLeft #time-label{font-size:1.5rem}.TimeLeft #time-left{padding:0.5rem 0;margin:0.7rem 0;font-weight:700;font-size:2rem;color:#222222;border-radius:10px}

*{padding:0;margin:0;box-sizing:border-box}.App{text-align:center;display:-webkit-flex;display:flex;-webkit-justify-content:space-around;justify-content:space-around;-webkit-align-items:center;align-items:center;font-family:"Poppins", sans-serif;height:100vh;width:100vw;color:#222222;background:#ececec}.increment-decrement-button{width:2.5rem;height:1.5rem;border-radius:7px;border:1px solid gray;margin:0.2rem;outline:none;font-weight:700;color:#222222}.timer-div{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;-webkit-flex-direction:column;flex-direction:column;background:#ff8690;height:20rem;width:20rem;border-radius:60%;box-shadow:0px 4px 4px rgba(0,0,0,0.25)}#resetButton,#startStopButton{width:4rem;height:2rem;border-radius:7px;border:1px solid gray;outline:none;font-weight:700;margin:0.1rem 0;color:#222222}#startStopButton{margin-top:0.7rem}.set-time-div{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;-webkit-flex-direction:column;flex-direction:column;width:10rem;height:8rem;border:2.5px solid #ff8690;border-radius:10px;box-shadow:0px 4px 4px rgba(0,0,0,0.25)}#session-div{margin-left:10rem}#break-div{margin-right:10rem}#session-length,#break-length{margin:0.3rem 0}@media only screen and (max-width: 1024px){.App{-webkit-flex-direction:column;flex-direction:column}#session-div,#break-div{margin:0}}

